* {
    text-decoration: none;
}

header {
    top: 0;
    left: 0;
    right: 0;
    background-color: transparent;
    display: flex;
    align-items: center;
    z-index: 999;
    width: 100%;
    padding: 0 30px;
    position: fixed;
    transition: all ease 0.5s;
}

header.black{
    background-color: rgba(14, 14, 14, 0.85);
}

header img{
    width: 120px;
}


.header--logo {
    font-size: 30px;
    text-transform: uppercase;
    text-shadow: 0 0 4px #db0000, 0 0 4px #fff;
}

.header--logo a, Link {
    color: #db0000;
    text-decoration: none;
    font-weight: bold;
}

.header--menu {
    list-style: none;
    margin-left: 30px;
}


.header--menu a:hover {
    text-shadow: 0 0 4px #fff, 0 0 4px #8b9dc3;
    transition: all ease 0.2s;
}


.dropdown .dropbtn {
    font-size: 17px;    
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
    font-weight: bold;
}

.dropdown-content {
    display: none;
    position: absolute;
    min-width: 160px;
    z-index: 1;
}

.dropdown-content a {
    float: none;
    background-color: #564d4d;
    color: #fff;
    padding: 5px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}
  
.topnav a:hover, .dropdown:hover .dropbtn {
   background-color: #db0000;
   color: white;
 }
  
.dropdown-content a:hover {
   background-color: #db0000;
   color: #ccc;
}
  
.dropdown:hover .dropdown-content {
   display: block;
}

.search form input[type=text] {
    border-radius: 5px;
    border: none;
    padding: 8px;
    margin: 5px;
    outline: none;
    color: black;
}