.movieRow {
    color: #ccc;
    margin-top: 70px;
}

.movieRow h2 {
    margin: 0 0 0 30px;
}

.movieRow--list {
    width: 100%;
}


.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}


.movieRow--item {
    display: inline-block;
    width: 180px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}


.movieRow--item img:hover {
    transform: scale(1);
}

.movieRow--left,
.movieRow--right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgb(0,0,0, 0.8);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5;
    color: whitesmoke;
}

.movieRow--left {
    left: 0;
}

.movieRow--right {
    right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
}


.movie {
    position: relative;
    overflow: hidden;
    height: 220px;
  }

.overview {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    padding: 1rem;
    max-height: 100%;
    transform: translateY(101%);
    transition: transform 0.3s ease-in;
    opacity: 0.9;
    color: #fff;
    text-align: center;
  }

  .movieRow--item:hover .overview {
    transform: translateY(0);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  