.footer {
    background-color: #111;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 50px 0;
    font-size: 12px;
    color: #ccc;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.footer p {
    max-width: 500px;
}
.footer a {
    color: #3b5998;
}